.content-container {
  max-width: 1200px;
  padding: 0 20px; }

.about-mini__title {
  font-weight: 900;
  margin-bottom: 20px; }
  .about-mini__title--slim {
    font-weight: 300;
    margin-bottom: 30px; }

.about-mini__p {
  margin: 16px 0; }

.about-mini__content {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-areas: 'b1 b2' 'b1 b3';
  padding: 80px 20px 50px; }

.about-mini__block-1 {
  grid-area: b1;
  padding-right: 60px; }

.about-mini__block-2 {
  grid-area: b2;
  margin-left: 25px; }

.about-mini__block-3 {
  color: black;
  background-color: white;
  grid-area: b3;
  margin-top: 20px;
  padding: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.about-mini__block-3-content {
  width: calc(50% - 40px); }

@media screen and (max-width: 600px) {
  .about-mini__content {
    display: block; }
  .about-mini__block-2 {
    margin-left: 0; }
  .about-mini__block-3-content {
    width: calc(50% - 5px); } }

.button-1 {
  font-weight: 700;
  padding: 5px 10px;
  border-style: solid;
  border-width: 3px; }

.contact-form__title {
  margin-bottom: 5px; }

.contact-form__input, .contact-form__textArea {
  width: 100%;
  max-width: 300px;
  margin-bottom: 14px; }

.contact {
  margin: 70px 0; }
  .contact__title {
    font-size: 40px;
    font-weight: 900;
    margin-bottom: 80px;
    position: relative;
    z-index: 1; }
  .contact__line {
    font-size: 100px;
    position: absolute;
    top: 5px;
    left: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: -1; }
  .contact__content {
    display: flex;
    flex-direction: row; }
  .contact__description {
    max-width: 520px;
    margin-right: 60px; }
  .contact__p {
    margin-bottom: 18px; }

@media screen and (max-width: 900px) {
  .contact__content {
    flex-direction: column; } }

.CTA {
  margin: 30px 0; }
  .CTA__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .CTA__welcome {
    max-width: 350px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .CTA__welcome-btn {
    margin-top: 30px;
    align-self: flex-end; }
  .CTA__welcome-title {
    font-size: 40px;
    font-weight: 900;
    margin-bottom: 80px;
    position: relative;
    z-index: 1; }
  .CTA__welcome-line {
    font-size: 100px;
    position: absolute;
    top: 5px;
    left: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: -1; }
  .CTA__design {
    max-width: 730px; }
  .CTA__design-img {
    width: 100%; }
  .CTA__design-text-block {
    padding: 30px 60px; }
  .CTA__design-title {
    font-size: 20px;
    font-weight: 900; }
  .CTA__design-p {
    margin: 15px 0; }

@media screen and (max-width: 600px) {
  .CTA__content {
    flex-direction: column;
    justify-content: space-between; }
  .CTA__welcome {
    max-width: none; } }

.site-logo {
  font-weight: 300;
  grid-area: site-logo; }
  .site-logo__bold {
    font-weight: 900; }

.drop-down-menu {
  position: absolute;
  display: none;
  z-index: 999999999; }
  .drop-down-menu__link {
    display: block; }

.navigation-primary {
  font-weight: 500;
  grid-area: primary-navigation; }
  .navigation-primary__main-link-box {
    display: inline-block;
    margin: 0 15px;
    position: relative; }
    .navigation-primary__main-link-box:hover .navigation-primary__drop-down {
      display: block; }
    .navigation-primary__main-link-box:first-child {
      margin-left: 0; }
    .navigation-primary__main-link-box:last-child {
      margin-right: 0; }
  .navigation-primary__main-link {
    display: inline-block;
    padding: 20px 0; }
  .navigation-primary__drop-down {
    width: 150px;
    padding: 3px;
    font-size: 12px;
    font-weight: 700;
    top: 55px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
    .navigation-primary__drop-down:before {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      border-width: 10px;
      border-style: solid;
      border-color: transparent transparent #1e2123 transparent;
      bottom: 100%;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%); }
  .navigation-primary__drop-down-link {
    padding: 8px 20px; }

.footer {
  padding: 40px 0 20px; }
  .footer__content {
    display: grid;
    grid-template-areas: 'text primary-navigation' 'text copy';
    grid-template-columns: 1fr 1fr; }
  .footer__nav-primary {
    align-self: start;
    justify-self: end; }
    .footer__nav-primary .navigation-primary__main-link {
      padding: 0; }
  .footer__text {
    grid-area: text;
    color: gray; }
  .footer__copyright {
    grid-area: copy;
    justify-self: end;
    align-self: end; }
  .footer__copyright-logo {
    display: inline-block;
    font-size: 16px; }

@media screen and (max-width: 960px) {
  .footer {
    text-align: center; }
    .footer__content {
      grid-template-areas: 'primary-navigation' 'text' 'copy';
      grid-template-columns: 1fr; }
    .footer__nav-primary {
      justify-self: center; }
    .footer__copyright {
      justify-self: center; } }

@media screen and (max-width: 600px) {
  .footer {
    padding: 20px 0 20px; }
    .footer__text {
      margin: 20px 0; }
    .footer__nav-primary {
      width: 100%; }
      .footer__nav-primary .navigation-primary__main-link {
        display: block;
        margin: 0;
        padding: 5px 0; }
      .footer__nav-primary .navigation-primary__main-link-box {
        display: block;
        margin: 0; } }

.navigation-secondary {
  font-size: 14px;
  font-weight: 300;
  grid-area: secondary-navigation;
  padding: 15px 0; }
  .navigation-secondary__divider {
    display: inline-block;
    padding: 0 8px; }

.header__grid {
  display: grid;
  grid-template-rows: 1fr -webkit-min-content 1fr;
  grid-template-rows: 1fr min-content 1fr;
  grid-template-areas: 'secondary-navigation secondary-navigation' 'site-logo primary-navigation' '... ...'; }

.header__nav-primary {
  align-self: center;
  justify-self: end; }

.header__site-logo {
  font-size: 28px;
  align-self: center; }

@media screen and (max-width: 650px) {
  .header__grid {
    display: block; }
  .header__site-logo {
    display: block;
    text-align: center;
    padding: 20px 0; }
  .header__nav-secondary {
    text-align: center;
    padding: 0; }
    .header__nav-secondary .navigation-secondary__link {
      display: block;
      margin: 10px 0; }
    .header__nav-secondary .navigation-secondary__divider {
      display: none; }
  .header__nav-primary {
    text-align: center;
    padding: 0; }
    .header__nav-primary .navigation-primary__main-link-box {
      display: block;
      padding: 0;
      margin: 0; }
    .header__nav-primary .navigation-primary__main-link {
      display: block;
      padding: 10px 0; }
    .header__nav-primary .navigation-primary__main-link-box:hover .drop-down-menu {
      display: none; } }

.main-container {
  max-width: 1400px; }

