.header__grid {
  display: grid;
  grid-template-rows: 1fr min-content 1fr;
  grid-template-areas: 'secondary-navigation secondary-navigation' 'site-logo primary-navigation' '... ...'; }

.header__nav-primary {
  align-self: center;
  justify-self: end; }

.header__site-logo {
  font-size: 28px;
  align-self: center; }

@media screen and (max-width: 650px) {
  .header__grid {
    display: block; }
  .header__site-logo {
    display: block;
    text-align: center;
    padding: 20px 0; }
  .header__nav-secondary {
    text-align: center;
    padding: 0; }
    .header__nav-secondary .navigation-secondary__link {
      display: block;
      margin: 10px 0; }
    .header__nav-secondary .navigation-secondary__divider {
      display: none; }
  .header__nav-primary {
    text-align: center;
    padding: 0; }
    .header__nav-primary .navigation-primary__main-link-box {
      display: block;
      padding: 0;
      margin: 0; }
    .header__nav-primary .navigation-primary__main-link {
      display: block;
      padding: 10px 0; }
    .header__nav-primary .navigation-primary__main-link-box:hover .drop-down-menu {
      display: none; } }
