.footer {
  padding: 40px 0 20px; }
  .footer__content {
    display: grid;
    grid-template-areas: 'text primary-navigation' 'text copy';
    grid-template-columns: 1fr 1fr; }
  .footer__nav-primary {
    align-self: start;
    justify-self: end; }
    .footer__nav-primary .navigation-primary__main-link {
      padding: 0; }
  .footer__text {
    grid-area: text;
    color: gray; }
  .footer__copyright {
    grid-area: copy;
    justify-self: end;
    align-self: end; }
  .footer__copyright-logo {
    display: inline-block;
    font-size: 16px; }

@media screen and (max-width: 960px) {
  .footer {
    text-align: center; }
    .footer__content {
      grid-template-areas: 'primary-navigation' 'text' 'copy';
      grid-template-columns: 1fr; }
    .footer__nav-primary {
      justify-self: center; }
    .footer__copyright {
      justify-self: center; } }

@media screen and (max-width: 600px) {
  .footer {
    padding: 20px 0 20px; }
    .footer__text {
      margin: 20px 0; }
    .footer__nav-primary {
      width: 100%; }
      .footer__nav-primary .navigation-primary__main-link {
        display: block;
        margin: 0;
        padding: 5px 0; }
      .footer__nav-primary .navigation-primary__main-link-box {
        display: block;
        margin: 0; } }
